@import "../../../scss/abstracts/variables";
@import "../../../scss/abstracts/mixins";

.tournaments-selector {
  @include position($position: relative, $z-index: 12);
  @include flex-center($gap: 1rem);
  margin: 0 auto;
  padding: 0.4rem 1.8rem;
  @include wh($width: 56rem, $height: 6rem);
  margin-bottom: 4rem;
  border-radius: 2.2rem;
  background: rgba($dark-blue, 0.9);
  backdrop-filter: blur(1rem);
  border: 0.1rem solid rgba($white, 0.2);
  box-shadow: 0 0.8rem 2.5rem rgba($black, 0.3);
}

.selector-img {
  filter: brightness(0) invert(1);
}

.selector-options {
  @include flex-center($gap: 1rem);
}

.selector-option__item {
  @include position($position: relative);
  @include grid($gap: 0.8rem);
  @include wh($width: 20rem, $height: 4.8rem);
  padding: 0.6rem 0.4rem;
  line-height: 1;

  background: rgba($dark-blue, 0.75);
  border: 0.1rem solid rgba($white, 0.18);
  border-radius: 1rem;
  cursor: pointer;

  transition: all 0.3s ease;
  &:hover {
    border: 0.1rem solid $yellow;
    background: rgba($dark-blue, 0.95);
    box-shadow: 0 0.5rem 1.5rem rgba($yellow, 0.2);
  }
}

.option__title {
  font-size: 1.2rem;
  font-weight: 400;
  color: rgba($light-grey, 0.8);
}

.option__value {
  font-size: 1.6rem;
  font-weight: 600;
  color: $white;
}

.selector__btn {
  @include flex-center($gap: 0.4rem);

  cursor: pointer;
  background: linear-gradient(135deg, #fdc52c 0%, #d4af37 100%);

  border: none;
  border-radius: 1rem;
  padding: 1rem 1.2rem;
  transition: all 0.3s ease;

  &:hover {
    background: linear-gradient(135deg, #e6bf3f 0%, #c5a641 100%);
    box-shadow: 0 0.5rem 1.5rem rgba($yellow, 0.4);
  }

  p {
    color: $white;
    font-weight: 600;
    font-size: 1.4rem;
  }
}

.selector__btn-img {
  filter: brightness(0) invert(1);
  @include square($size: 1.6rem);
}

.selector-dropdown {
  @include position($position: absolute, $top: calc(100% + 0.6rem), $left: -20%, $z-index: 14);
  width: 140%;
  padding: 0.8rem 0.4rem;
  background: rgba($dark-blue, 0.95);
  backdrop-filter: blur(1.5rem);
  border: 0.1rem solid rgba($white, 0.25);
  border-radius: 1rem;
  box-shadow: 0 1rem 3rem rgba($black, 0.4);
}

.dropdown__list {
  @include grid($gap: 0.4rem);
}

.dropdown__item {
  position: relative;
  padding: 1rem 1.2rem;
  font-size: 1.6rem;
  font-weight: 500;
  color: #ffffff;
  border-radius: 0.6rem;
  transition: all 0.2s ease;
  cursor: pointer;

  &:hover {
    font-weight: 600;
    background: rgba($white, 0.15);
    color: $yellow;
  }

  &.active {
    background: rgba($white, 0.1);

    &::before {
      content: "";
      @include position($position: absolute, $left: 0, $top: 50%, $transform: translateY(-50%));
      @include wh($width: 0.4rem, $height: 70%);

      background: linear-gradient(135deg, #fdc52c 0%, #ffb300 100%);
      border-radius: 0 0.2rem 0.2rem 0;
    }
  }
}

.selector-dropdown {
  animation: dropdownAppear 0.2s ease;
}

@keyframes dropdownAppear {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
